<template>
    <a-menu :default-selected-keys="defaultSelectedKeys" :default-open-keys="defaultOpenKeys" mode="inline" theme="dark"
    @select="changeRoute"
    >
        <template v-for="item in routes">
            <a-sub-menu :key="item.path">
                <span slot="title">
                    <a-icon type="appstore" />
                    <span>{{item.meta.title}}</span>
                </span>
                <a-menu-item v-for="childRoute in item.children" :key="childRoute.path">
                    {{childRoute.meta.title}}
                </a-menu-item>
                
            </a-sub-menu>

        </template>
    </a-menu>
</template>
<script>
import { appRouters, cankaoRouters } from '@/router/routes'
export default {
  methods:{
    changeRoute({ item, key, selectedKeys }){
      console.log(item)
      console.log(key)
      console.log(selectedKeys)
      this.$router.push(key)
    }
  },
  data(){
    return {
      routes:[...cankaoRouters,...appRouters],
      defaultSelectedKeys:[],
      defaultOpenKeys:[]

    }
  }
}
</script>
<style lang="less" scoped>
</style>