import Vue from 'vue'
import VueRouter from 'vue-router'
import { routes } from './routes'
import { setCookie } from '@/utils/cookie'
import { Modal } from 'ant-design-vue'
Vue.use(VueRouter)

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  Modal.destroyAll();
  if(process.env.NODE_ENV == 'development'){
    if(!abp.session.userId && to.path !== '/login'){
      let oHead = document.getElementsByTagName('head')[0]; // 在head标签中创建创建script
      let oScript = document.createElement("script");
      oScript.type = "text/javascript";
      oScript.src = "api/AbpScripts/GetScripts";
      oHead.appendChild(oScript);
      oScript.onload = function(){
         console.log(JSON.stringify(abp.session))
         next()
      }
    }else{
      next()
    }
  }else{
    console.log('router',JSON.stringify(abp.session))
    next()
  }
})

router.afterEach((to, from) => {
  let pathname = location.pathname;
  let currPath = pathname+location.hash;
  setCookie('page_name',currPath)
})

export default router
