import Index from '@/components/layout/layout'
export const loginRouter = {
    path: '/login',
    name: 'login',
    meta: {
        title: '登录'
    },
    component: () => import('@/views/login/index.vue')
}

export const cankaoRouters = [{
    path: '/',
    name: '',
    redirect:'/home',
    meta: {
        title: '参考页面'
    },
    component: Index,
    children: [{
        path: '/home',
        name: '',
        meta: {
            title: '用户维护',
            desc:'管理用户及权限.'
        },
        component: () => import('@/views/cankaopage/index.vue')
    }]
}]



export const appRouters = []


export const routes = [loginRouter,...cankaoRouters, ...appRouters]